.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.square {
  border: 1px solid #999;
  text-align: left;
  padding: 4px;  
  -ms-flex-positive: 1;  
      flex-grow: 1;
  width: 33%;
}

.board {
  width: 450px;
  height: 450px;
  border: 25px solid #768;
  margin: 0 auto;
  border-radius: 5px;
  -webkit-box-shadow: 0px 10px 30px #666;
          box-shadow: 0px 10px 30px #666;
  
  display:-ms-flexbox;
  
  display:flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.container {
  width: 500px;
  text-align: center;
  margin: 20px auto;
}

@media (max-width: 500px) {
  .container {
    width: 100%
  }

  .board {
    width: 300px;
    height: 300px;
  }  
}




